.main-nav__login-link {
  width: 100%;
  display: block;
  text-align: center;
  padding: 16px 0;
  border-radius: 100px;
  margin-bottom: 51px;
  text-transform: uppercase;
  letter-spacing: 0.3px;

  @media (min-width: $tablet-width) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.main-nav__icon-phone,
.main-nav__icon-email {
  display: inline-block;
  width: 45px;
  height: 45px;
  border-radius: 100px;
  background-color: #121936;
  position: relative;
  margin-right: 16px;

  @media (min-width: $tablet-width) {
    width: 68px;
    height: 68px;
    margin-right: 23px;
  }
}

.main-nav__phone-box,
.main-nav__email-box {
  @media (min-width: $tablet-width) {
    margin-left: 55px;
  }
}

.main-nav__phone-link,
.main-nav__email-link {
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  text-transform: uppercase;
  color: $color-dark-blue-1;
  display: flex;
  align-items: center;

  @media (min-width: $tablet-width) {
    font-size: 28px;
    line-height: 28px;
  }
}

.main-nav__icon-phone svg,
.main-nav__icon-email svg {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  transform: translate(-50%, -50%) scale(0.5);
  fill: $color-yellow-1;

  @media (min-width: $tablet-width) {
    transform: translate(-50%, -50%) scale(0.7);
  }

  &:hover {
    fill: $color-yellow-2;
  }

  &:active {
    fill: $color-yellow-2;
    opacity: 30%;
  }
}
